<template>
  <div>
    <div class="page-subtitle">
      <h4>{{localize('Create')}}</h4>
    </div>

    <form @submit.prevent="submitHandler">
      <div class="input-field">
        <input
            id="name"
            type="text"
            v-model="title"
            :class="{invalid: $v.title.$error}"
        >
        <label for="name">{{localize('Title')}}</label>
        <span 
          v-if="$v.title.$error"
          class="helper-text invalid"
        >
          {{localize('Message_CategoryTitle')}}
        </span>
      </div>

      <div class="input-field">
        <input
            id="limit"
            type="number"
            v-model.number="limit"
            :class="{invalid: $v.limit.$error}"
        >
        <label for="limit">Лимит</label>
        <span 
          v-if="$v.limit.$error"
          class="helper-text invalid"
        >
          {{localize('Message_MinLength')}} {{$v.limit.$params.minValue.min}}
        </span>
      </div>

      <button class="btn waves-effect waves-light" type="submit">
        {{localize('Create')}}
        <i class="material-icons right">send</i>
      </button>
    </form>
  </div>
</template>

<script>
import {required, minValue} from 'vuelidate/lib/validators'
import locale from '@/mixins/locale.mixin'

export default {
  data: () => ({
    title: '',
    limit: 100
  }),
  validations: {
    title: {required},
    limit: {minValue: minValue(100)}
  },
  mixins: [locale],
  mounted() {
    M.updateTextFields()
  },
  methods: {
    async submitHandler() {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }

      try {
        const category = await this.$store.dispatch('createCategory', {
          title: this.title,
          limit: this.limit
        })
        this.title = ''
        this.limit = 100
        this.$v.$reset()
        this.$message(this.localize('Category_HasBeenCreated'))
        this.$emit('created', category)
      } catch (e) {}
    }
  }
}
</script>